import React from 'react'
import {Button} from '../ButtonElement'
import {InfoContainer,InfoRow, InfoWrapper, HorizontalLine,TextWrapper, Column1, TopLine, Heading, Subtitle, Column2, Img, BtnWrap, ImgWrap} from './InfoElements';


const InfoSection = ({
    lightBg,
    id,
    imgStart,
    topLine,
    lightText,
    headline,
    darkText,
    description,
    buttonLabel,
    img,
    alt,
    primary,
    dark,
    dark2
}) => {
    return (
        <>
            <InfoContainer lightBg={lightBg} id={id}>
                  <InfoWrapper>
                      <InfoRow imgStart={imgStart}>
                            <Column1>
                                <TextWrapper>
                                    <TopLine>{topLine}</TopLine>
                                    
                                    <Heading lightText={lightText}>{headline}</Heading>
                                    <HorizontalLine lightBg={lightBg}/>
                                    <br />
                                    <Subtitle darkText={darkText}>{description}</Subtitle>
                                    <HorizontalLine lightBg={lightBg}/>
                                    <br />
                                    <BtnWrap>
                                        <Button to='/' 
                                        smooth={true}
                                        duration={500}
                                        spy={true}
                                        exact="true"
                                        offset={-80}
                                        primary={primary ? 1 : 0}
                                        dark = {dark ? 1: 0}
                                        dark2 = {dark2 ? 1 : 0}

                                        >{buttonLabel}</Button>
                                    </BtnWrap>
                                </TextWrapper>
                            </Column1>
                            <Column2>
                                <ImgWrap>
                                    <Img src={img} alt={alt}/>
                                </ImgWrap>
                            </Column2>
                      </InfoRow>
                  </InfoWrapper>
            </InfoContainer>  
        </>
    )
}

export default InfoSection
