import styled from 'styled-components';
import {MdKeyboardArrowRight, MdArrowForward} from 'react-icons/md';

export const HeroContainer = styled.div`
    background: #0c0c0c;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 30px;
    height: 800px;
    position: relative;
    z-index: 1;

    :before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: linear-gradient(180deg, rgba(0,0,0,0.8) 0%,
        rgba(0,0,0,0.6) 100%),
        linear-gradient(180deg, rgba(0,0,0,0.8) 0%, transparent 100%);
        z-index: 2;
    }
`


export const HeroBg = styled.div`
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
`

export const VideoBg = styled.video`
    width: 100%;
    height: 100%;
    -o-object-fit: cover;
    object-fit: cover;
    background: #232a34;
`

export const HeroContent = styled.div`
    z-index: 3;
    max-width: 1200px;
    position: absolute;
    padding: 8px 24px;
    display: flex;
    flex-direction: column;
    align-items: center;
`

export const HeroH1 = styled.h1`
    color: #fff;
    font-size: 22px;
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 0.3rem;
    word-spacing: 0.7rem;

    @media screen and (max-width: 768px) {
        font-size: 18px;
        letter-spacing: 0.1rem;
        word-spacing: 0.3rem;
    } 

    @media screen and (max-width: 480px) {
        font-size: 16px;
        letter-spacing: 0.05rem;
        word-spacing: 0.2rem;
    }
`

export const HorizontalLine = styled.hr`
    width: 50%;
    list-style: none;
`

export const HeroP = styled.p`
    margin-top: 24px;
    color: #fff;
    font-size: 24px;
    text-align: center;
    max-width: 600px;

    @media screen and (max-width: 768px) {
        font-size: 24px;
    } 

    @media screen and (max-width: 480px) {
        font-size: 18px;
    }
`

export const HeroBtnWrapper = styled.div`
    margin-top: 32px;
    display: flex;
    flex-direction: column;
    align-items: center;
`

export const ArrowForward = styled(MdArrowForward)`
    margin-left: 8px;
    font-size: 30px;
`

export const ArrowRight = styled(MdKeyboardArrowRight)`
    margin-left: 8px;
    font-size: 30px;
`
export const QuotesOpen = styled.span`
    font-size: xx-large;
    text-align: left;
    color: #65D6F9;
`

export const QuotesClose = styled.span`
    font-size: xx-large;
    text-align: right;
    color: #65D6F9;
`

export const ColorAccent = styled.span`
    color: #65D6F9;
`